*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  min-width: 320px;
  max-width: 1920px;
  margin: 0 auto;
  font-family: 'Arial', sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 14px;
  line-height: 16px;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  margin: -1px;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.hidden {
  display: none;
}

.ads {
  display: flex;
  flex-wrap: wrap;
  padding: 30px 15px 30px 30px;
}

.ad {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 300px;
  margin-bottom: 30px;
  margin-right: 20px;
  color: #000000;
  text-decoration: none;
}

.ad__title {
  margin: 0;
  padding: 15px 0;
}

.ad__preview {
  margin-bottom: auto;
  padding: 15px 0;
}

.ad__address {
  margin: 0;
  margin-bottom: 10px;
}

.ad__price {
  margin: 0;
  font-weight: bold;
  font-size: 16px;
}

.ad-current {
  padding: 30px 15px 30px 30px;
}

.ad-current__previews {
  display: flex;
  flex-wrap: wrap;
}

.ad-current__full-image-container {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1400px;
  height: 500px;
  margin-bottom: 30px;
}

.ad-current__full-image {
  max-width: 100%;
  max-height: 100%;
}

.ad-current__title {
  margin-bottom: 40px;
}

.ad-current__address {
  font-size: 18px;
}

.ad-current__price {
  font-weight: bold;
  font-size: 20px;
}

.ad-current__description {
  max-width: 1000px;
}

.ad-current__name {
  font-weight: bold;
  font-size: 18px;
}

.ad-current__exit {
  text-decoration: none;
  border-bottom: 1px solid;
}

.preview-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 150px;
  margin-right: 20px;
  margin-bottom: 20px;
  background-color: #000000;
}

.preview {
  max-width: 100%;
  max-height: 100%;
}

.preview:hover {
  cursor: pointer;
}
